  margin-right: auto;
  max-width: 400px;
  flex-direction: column;
  align-items: center;
}
.Form_form__2TM1N input {
  margin-bottom: 10px;
}

.item-enter {
  transform: translateX(-100%);
}
.item-enter-active {
  transform: translateX(0);
  transition: transform 250ms ease-in;
}
.item-exit {
  transform: translateX(0);
}
.item-exit-active {
  transform: translateX(-100%);
  transition: transform 250ms ease-in;
}

